.mejs__offscreen {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  word-wrap: normal; }

.mejs__container {
  background: #000;
  box-sizing: border-box;
  font-family: 'Helvetica', Arial, serif;
  position: relative;
  text-align: left;
  text-indent: 0;
  vertical-align: top;
  * {
    box-sizing: border-box; }
  video {
    &::-webkit-media-controls, &::-webkit-media-controls-panel, &::-webkit-media-controls-panel-container, &::-webkit-media-controls-start-playback-button {
      -webkit-appearance: none;
      display: none !important; } } }

/* Hide native play button and control bar from iOS to favor plugin button */

.mejs__fill-container {
  height: 100%;
  width: 100%;
  .mejs__container {
    height: 100%;
    width: 100%; }
  background: transparent;
  margin: 0 auto;
  overflow: hidden;
  position: relative; }

.mejs__container:focus {
  outline: none; }

.mejs__iframe-overlay {
  height: 100%;
  position: absolute;
  width: 100%; }

.mejs__embed {
  background: #000;
  height: 100%;
  margin: 0;
  overflow: hidden;
  padding: 0;
  width: 100%;
  body {
    background: #000;
    height: 100%;
    margin: 0;
    overflow: hidden;
    padding: 0;
    width: 100%; } }

.mejs__fullscreen {
  overflow: hidden !important; }

.mejs__container-fullscreen {
  bottom: 0;
  left: 0;
  overflow: hidden;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 1000;
  .mejs__mediaelement, video {
    height: 100% !important;
    width: 100% !important; } }

/* Start: LAYERS */

.mejs__background {
  left: 0;
  position: absolute;
  top: 0; }

.mejs__mediaelement {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: 0; }

.mejs__poster {
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-size: cover;
  left: 0;
  position: absolute;
  top: 0;
  z-index: 1; }

 :root .mejs__poster-img {
  display: none; }

.mejs__poster-img {
  border: 0;
  padding: 0; }

.mejs__overlay {
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  left: 0;
  position: absolute;
  top: 0; }

.mejs__layer {
  z-index: 1; }

.mejs__overlay-play {
  cursor: pointer; }

.mejs__overlay-button {
  background: url('../img/mejs-controls.svg') no-repeat;
  background-position: 0 -39px;
  height: 80px;
  width: 80px; }

.mejs__overlay:hover > .mejs__overlay-button {
  background-position: -80px -39px; }

.mejs__overlay-loading {
  height: 80px;
  width: 80px; }

.mejs__overlay-loading-bg-img {
  -webkit-animation: mejs__loading-spinner 1s linear infinite;
  animation: mejs__loading-spinner 1s linear infinite;
  background: transparent url('../img/mejs-controls.svg') -160px -40px no-repeat;
  display: block;
  height: 80px;
  width: 80px;
  z-index: 1; }

@-webkit-keyframes mejs__loading-spinner {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }


@keyframes mejs__loading-spinner {
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }


/* End: LAYERS */

/* Start: CONTROL BAR */

.mejs__controls {
  bottom: -70px;
  display: flex;
  align-items: center;
  height: 40px;
  left: 0;
  list-style-type: none;
  margin: 0;
  padding: 0 0px;
  position: absolute;
  width: 100%;
  z-index: 3;
  opacity: 1 !important;
  transform: translate3d(0,0,1);
  transform-style: preserve-3d;
  &:not([style*='display: none']) {
    // background: rgba(255, 0, 0, 0.7)
    // background: -webkit-linear-gradient(transparent, rgba(0, 0, 0, 0.35))
    // background: linear-gradient(transparent, rgba(0, 0, 0, 0.35))
    // background: rgba(#000, .1)
 } }    // opacity: 1 !important

.mejs__audio {
  background: transparent;
  .mejs__controls {
    bottom: 0; } }

.mejs__button, .mejs__time, .mejs__time-rail {
  position: relative;
  font-size: 10px;
  height: 40px;
  line-height: 10px;
  margin: 0;
  width: 40px; }

.mejs__button {
  flex: 0 0 auto; }
.mejs__button > button {
  background: transparent url('../img/mejs-controls.svg');
  border: 0;
  cursor: pointer;
  display: block;
  font-size: 0;
  height: 20px;
  line-height: 0;
  // margin: 10px 6px
  overflow: hidden;
  padding: 0;
  position: absolute;
  text-decoration: none;
  width: 20px;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
  &:focus {
 } }    // outline: dotted 1px #999

/* :focus for accessibility */

.mejs__container-keyboard-inactive {
  a, button, [role=slider] {
    outline: 0;
    &:focus {
      outline: 0; } } }

/* End: CONTROL BAR */

/* Start: Time (Current / Duration) */

.mejs__time {
  box-sizing: content-box;
  color: $fontColor;
  font-size: 12px;
  overflow: hidden;
  text-align: center;
  width: auto;
  display: flex;
  align-items: center; }

/* End: Time (Current / Duration) */

/* Start: Play/Pause/Stop */

.mejs__play, .mejs__pause {
  background: $colorRed;
  border-radius: 50%;
  margin-right: 27px;
  box-shadow: 0 4px 10px 0 rgba(255,39,123,0.33);
  cursor: pointer;
  transition: .25s $ease;
  &:hover {
    box-shadow: 0 2px 5px 0 rgba(255,39,123,0.13); } }

.mejs__play {
  > button {
    background-position: 0 0;
    transform: translate(-45%,-45%); } }

.mejs__pause {
  > button {
    background-position: -20px 0;
    transform: translate(-45%,-45%); } }

.mejs__replay > button {
  background-position: -160px 0; }

/* End: Play/Pause/Stop */

/* Start: Progress Bar */

.mejs__time-rail {
  direction: ltr;
  -webkit-box-flex: 1;
  -webkit-flex-grow: 1;
  -ms-flex-positive: 1;
  flex-grow: 1;
  height: 40px;
  margin: 0 10px;
  padding-top: 10px;
  position: relative; }

.mejs__time-total, .mejs__time-buffering, .mejs__time-loaded, .mejs__time-current, .mejs__time-float, .mejs__time-hovered, .mejs__time-float-current, .mejs__time-float-corner, .mejs__time-marker {
  border-radius: 2px;
  cursor: pointer;
  display: block;
  height: 2px;
  position: absolute; }

.mejs__time-total {
  background: rgba(#979797, 0.13);
  margin: 9px 0 0;
  width: 100%; }

.mejs__time-buffering {
  -webkit-animation: buffering-stripes 2s linear infinite;
  animation: buffering-stripes 2s linear infinite;
  background: -webkit-linear-gradient(135deg, rgba(255, 255, 255, 0.4) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0.4) 75%, transparent 75%, transparent);
  background: linear-gradient(-45deg, rgba(255, 255, 255, 0.4) 25%, transparent 25%, transparent 50%, rgba(255, 255, 255, 0.4) 50%, rgba(255, 255, 255, 0.4) 75%, transparent 75%, transparent);
  background-size: 15px 15px;
  width: 100%; }

@-webkit-keyframes buffering-stripes {
  from {
    background-position: 0 0; }

  to {
    background-position: 30px 0; } }


@keyframes buffering-stripes {
  from {
    background-position: 0 0; }

  to {
    background-position: 30px 0; } }


.mejs__time-loaded {
  background: rgba(255, 255, 255, 0.3); }

.mejs__time-current, .mejs__time-handle-content {
  background: rgba(255, 255, 255, 0.9);
  background: linear-gradient(to right, #8E5ACD, #21B0B0); }

.mejs__time-hovered {
  background: rgba(255, 255, 255, 0.5);
  z-index: 10;
  &.negative {
    background: rgba(0, 0, 0, 0.2); } }

.mejs__time-current, .mejs__time-buffering, .mejs__time-loaded, .mejs__time-hovered {
  left: 0;
  -webkit-transform: scaleX(0);
  -ms-transform: scaleX(0);
  transform: scaleX(0);
  -webkit-transform-origin: 0 0;
  -ms-transform-origin: 0 0;
  transform-origin: 0 0;
  -webkit-transition: 0.15s ease-in all;
  transition: 0.15s ease-in all;
  width: 100%; }

.mejs__time-buffering {
  -webkit-transform: scaleX(1);
  -ms-transform: scaleX(1);
  transform: scaleX(1); }

.mejs__time-hovered {
  -webkit-transition: height 0.1s cubic-bezier(0.44, 0, 1, 1);
  transition: height 0.1s cubic-bezier(0.44, 0, 1, 1);
  &.no-hover {
    -webkit-transform: scaleX(0) !important;
    -ms-transform: scaleX(0) !important;
    transform: scaleX(0) !important; } }

.mejs__time-handle {
  // border: 4px solid transparent
  size: 15px;
  cursor: pointer;
  left: -7px;
  top: -7px;
  position: absolute;
  -webkit-transform: translateX(0);
  -ms-transform: translateX(0);
  transform: translateX(0);
  z-index: 11;
  background: #FFFFFF;
  border-radius: 50%;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.21);
 }  // transition: .1s linear

.mejs__time-handle-content {
  // display: none
  // border: 4px solid transparent
  cursor: pointer;
  left: 0;
  position: absolute;
  transform: translateX(0);
  z-index: 11;
  // border: 4px solid rgba(255, 255, 255, 0.9)
  border-radius: 50%;
  height: 10px;
  left: -7px;
  top: -4px;
  background: white;
  opacity: 0;
  transform: scale(0);
  size: 15px; }

.mejs__time-rail {
  &:hover .mejs__time-handle-content {
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1); }
  .mejs__time-handle-content {
    &:focus, &:active {
      -webkit-transform: scale(1);
      -ms-transform: scale(1);
      transform: scale(1); } } }

.mejs__time-float {
  background: #eee;
  border: solid 1px #333;
  bottom: 100%;
  color: #111;
  display: none;
  height: 17px;
  margin-bottom: 9px;
  position: absolute;
  text-align: center;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 36px; }

.mejs__time-float-current {
  display: block;
  left: 0;
  margin: 2px;
  text-align: center;
  width: 30px; }

.mejs__time-float-corner {
  border: solid 5px #eee;
  border-color: #eee transparent transparent;
  border-radius: 0;
  display: block;
  height: 0;
  left: 50%;
  line-height: 0;
  position: absolute;
  top: 100%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 0; }

.mejs__long-video {
  .mejs__time-float {
    margin-left: -23px;
    width: 64px; }
  .mejs__time-float-current {
    width: 60px; } }

.mejs__broadcast {
  color: #fff;
  height: 10px;
  position: absolute;
  top: 15px;
  width: 100%; }

/* End: Progress Bar */

/* Start: Fullscreen */

.mejs__fullscreen-button > button {
  @include sprite(fullscreen); }

.mejs__unfullscreen > button {
  // refactor - missing icon
  @include sprite(fullscreen); }

/* End: Fullscreen */

/* Start: Mute/Volume */

.mejs__mute, .mejs__unmute {
  margin-left: 13px; }

.mejs__mute > button {
  @include sprite('soundbar'); }

.mejs__unmute > button {
  // refactor - need muted state
  @include sprite('soundbar'); }

.mejs__volume-button {
  position: relative;
  > .mejs__volume-slider {
    -webkit-backface-visibility: hidden;
    background: #FFFFFF;
    box-shadow: 10px 0 30px 0 rgba(0,0,0,0.08);
    border-radius: 6px;
    bottom: 100%;
    display: none;
    height: 115px;
    left: 50%;
    margin: 0;
    position: absolute;
    transform: translateX(-50%);
    width: 25px;
    z-index: 1; }
  &:hover {
    border-radius: 0 0 4px 4px; } }

.mejs__volume-total {
  background: rgba(#979797, 0.13);
  height: 100px;
  left: 50%;
  margin: 0;
  position: absolute;
  top: 8px;
  transform: translateX(-50%);
  width: 2px; }

.mejs__volume-current {
  background: linear-gradient(to top, #8E5ACD, #21B0B0);
  left: 0;
  margin: 0;
  position: absolute;
  width: 100%; }

.mejs__volume-handle {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.21);
  border-radius: 50%;
  cursor: ns-resize;
  size: 14px;
