.selectize-control.plugin-drag_drop {
  &.multi > .selectize-input > div.ui-sortable-placeholder {
    visibility: visible !important;
    background: #f2f2f2 !important;
    background: rgba(0, 0, 0, 0.06) !important;
    border: 0 none !important;
    box-shadow: inset 0 0 12px 4px #ffffff; }
  .ui-sortable-placeholder::after {
    content: '!';
    visibility: hidden; }
  .ui-sortable-helper {
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2); } }

.selectize-dropdown-header {
  position: relative;
  padding: 5px 8px;
  border-bottom: 1px solid #d0d0d0;
  background: #f8f8f8;
  border-radius: 3px 3px 0 0; }

.selectize-dropdown-header-close {
  position: absolute;
  right: 8px;
  top: 50%;
  color: #303030;
  opacity: 0.4;
  margin-top: -12px;
  line-height: 20px;
  font-size: 20px !important;
  &:hover {
    color: #000000; } }

.selectize-dropdown.plugin-optgroup_columns {
  .optgroup {
    border-right: 1px solid #f2f2f2;
    border-top: 0 none;
    float: left;
    box-sizing: border-box;
    &:last-child {
      border-right: 0 none; }
    &:before {
      display: none; } }
  .optgroup-header {
    border-top: 0 none; } }

.selectize-control {
  &.plugin-remove_button {
    [data-value] {
      position: relative;
      padding-right: 33px !important;
      .remove {
        z-index: 1;
        /* fixes ie bug (see #392) */
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        width: 25px;
        text-align: center;
        font-weight: 500;
        font-size: 14px;
        color: inherit;
        text-decoration: none;
        vertical-align: middle;
        display: inline-block;
        padding: 10px 2px 0 0;
        border-left: 1px solid rgba(#8C5BCC, .4);
        border-radius: 0 30px 30px 0;
        box-sizing: border-box;
        transition: .25s $ease;
        &:hover {
          background: rgba(0, 0, 0, 0.05); } }
      &.active .remove {
        border-left-color: rgba(#8C5BCC, .5); } }
    .disabled [data-value] .remove {
      &:hover {
        background: none; }
      border-left-color: #aaaaaa; }
    .remove-single {
      position: absolute;
      right: 28px;
      top: 6px;
      font-size: 23px; } }
  position: relative; }

.selectize-dropdown {
  color: #303030;
  font-family: inherit;
  font-size: 13px;
  line-height: 18px;
  -webkit-font-smoothing: inherit; }

.selectize-input {
  color: $fontColor;
  font-family: inherit;
  font-size: 16px;
  -webkit-font-smoothing: inherit;
  input {
    color: $fontColor;
    font-family: inherit;
    font-size: 16px;
    transition: 0s !important;
    -webkit-font-smoothing: inherit; }
  background: #ffffff;
  cursor: text;
  display: inline-block; }

.selectize-control.single .selectize-input.input-active {
  background: #ffffff;
  cursor: text;
  display: inline-block; }

.selectize-input {
  border: 0;
  padding: 21px 30px 20px;
  display: inline-block;
  width: 100%;
  overflow: hidden;
  position: relative;
  z-index: 1;
  box-sizing: border-box;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.07);
  border-radius: 8px;
  transition: box-shadow .25s $ease;
  &:hover {
    box-shadow: 0 4px 8px rgba(0,0,0,0.04); } }

.selectize-control.multi .selectize-input.has-items {
  padding: 11px 17px 8px; }

.selectize-input {
  &.full {
    background-color: #ffffff; }
  &.disabled {
    cursor: default !important;
    * {
      cursor: default !important; } }
  &.focus {
    box-shadow: 0 4px 8px rgba(0,0,0,0.2); }
  &.dropdown-active {
    border-radius: 8px 8px 0 0; }
  > * {
    vertical-align: baseline;
    display: -moz-inline-stack;
    display: inline-block;
    zoom: 1;
    *display: inline; } }

.selectize-control.multi .selectize-input {
  > div {
    cursor: pointer;
    margin: 0 3px 3px 0;
    padding: 10px 25px 10px 15px;
    background: rgba(#8E5ACD, .18);
    color: #8C5BCC;
    border: 0;
    transition: background .25s $ease;
    &.active {
      background: rgba(#8E5ACD, .28);
      color: #8C5BCC;
      border: 0; } }
  &.disabled > div {
    color: #ffffff;
    background: #d2d2d2;
    border: 0;
    &.active {
      color: #ffffff;
      background: #d2d2d2;
      border: 1px solid #aaaaaa; } } }

.selectize-input {
  > input {
    display: inline-block !important;
    padding: 0 !important;
    min-height: 0 !important;
    max-height: none !important;
    max-width: 100% !important;
    margin: 0 1px !important;
    text-indent: 0 !important;
    border: 0 none !important;
    background: none !important;
    line-height: inherit !important;
    -webkit-user-select: auto !important;
    box-shadow: none !important;
    transition: 0s !important;
    &::-ms-clear {
      display: none; }
    &:focus {
      outline: none !important; } }
  &::after {
    content: ' ';
    display: block;
    clear: left; }
  &.dropdown-active::before {
    content: ' ';
    display: block;
    position: absolute;
    background: #f0f0f0;
    height: 1px;
    bottom: 0;
    left: 0;
    right: 0; } }

.selectize-dropdown {
  position: absolute;
  z-index: 10;
  border: 0;
  background: #ffffff;
  margin: -1px 0 0 0;
  border-top: 0 none;
  box-sizing: border-box;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.07);
  border-radius: 0 0 8px 8px;
  [data-selectable] {
    cursor: pointer;
    overflow: hidden;
    padding: 8px 20px;
    transition: .25s $ease;
    .highlight {
      background: rgba(125, 168, 208, 0.2);
      border-radius: 1px; } }

  .optgroup-header {
    padding: 5px 8px; }
  .optgroup:first-child .optgroup-header {
    border-top: 0 none; }
  .optgroup-header {
    color: #303030;
    background: #ffffff;
    cursor: default; }
  .active {
    background-color: #f5fafd;
    color: #495c68;
    &.create {
      color: #495c68; } }
  .create {
    color: rgba(48, 48, 48, 0.5); } }

.selectize-dropdown-content {
  overflow-y: auto;
  overflow-x: hidden;
  max-height: 200px;
  -webkit-overflow-scrolling: touch; }

.selectize-control {
  &.single .selectize-input {
    cursor: pointer;
    input {
      cursor: pointer; }
    &.input-active {
      cursor: text;
      input {
        cursor: text; } }
    &:after {
      content: ' ';
      display: block;
      position: absolute;
      top: 50%;
      right: 15px;
      margin-top: -3px;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 5px 5px 0 5px;
      border-color: #808080 transparent transparent transparent; }
    &.dropdown-active:after {
      margin-top: -4px;
      border-width: 0 5px 5px 5px;
      border-color: transparent transparent #808080 transparent; } }
  &.rtl {
    &.single .selectize-input:after {
      left: 15px;
      right: auto; }
    .selectize-input > input {
      margin: 0 4px 0 -2px !important; } }
  .selectize-input.disabled {
    opacity: 0.5;
    background-color: #fafafa; }
  &.multi .selectize-input {
    &.has-items {
      padding-left: 11px;
      padding-right: 11px; }
    &.disabled [data-value] {
      color: #999;
      text-shadow: none;
      background: none;
      box-shadow: none;
      border-color: #e6e6e6;
      .remove {
        border-color: #e6e6e6;
        background: none; } }
    [data-value] {
      text-shadow: 0;
      border-radius: 30px;
      background: rgba(#8C5BCC, .18);
      box-shadow: 0;
      &.active {
        background: rgba(#8E5ACD, .28);
        color: #8C5BCC; } } }
  &.single .selectize-input {
    background: rgba(#8E5ACD, .18);
    color: #8C5BCC; } }

.selectize-dropdown {
  &.single {
    border-color: #b8b8b8; }
  .optgroup-header {
    padding-top: 7px;
    font-weight: bold;
    font-size: 0.85em; }
  .optgroup {
    border-top: 1px solid #f0f0f0;
    &:first-child {
      border-top: 0 none; } } }


@include r($tablet) {
  .selectize-control.multi .selectize-input > div {
    padding-top: 7px;
    font-size: 14px;
    padding-bottom: 7px; }

  .selectize-control {
    &.plugin-remove_button {
      [data-value] {
        .remove {
          padding-top: 6px; } } } } }
