/*======================================
 *  Selectric v1.13.0
 *====================================== */

.selectric-wrapper {
  position: relative;
  cursor: pointer; }

.selectric-responsive {
  width: 100%; }

.selectric {
  width: 100%;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 8px;
  background: white;
  position: relative;
  color: $fontColor;
  overflow: hidden;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.07);
  transition: .25s $ease;
  text-align: left;
  .label {
    flex: 1 1 auto;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 10px 0 0px;
    font-size: 16px;
    color: $fontColor;
    user-select: none;
    padding: 21px 30px 20px; }
  .button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 36px;
    height: 27px;
    font-size: 5px;
    background-color: transparent;
    color: $colorGreen;
    text-align: center;
    border: 1px solid $borderColor;
    border-radius: 15px;
    margin-right: 15px;
    transition: .25s $ease; } }

.selectric-focus .selectric {
 }  // border-color: #aaaaaa

.selectric-hover .selectric {
  box-shadow: 0 4px 8px rgba(0,0,0,0.04);
  .button {
    color: #a2a2a2; } }


.selectric-open {
  z-index: 9999;
  .selectric {
    // border-color: #c4c4c4
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    .button {
      transform: rotate(180deg); } }
  .selectric-items {
    display: block; } }

.selectric-disabled {
  filter: alpha(opacity = 50);
  opacity: 0.5;
  cursor: default;
  user-select: none; }

.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
  select {
    position: absolute;
    left: -100%; }
  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0; } } }

.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }

.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }

/* Items box */

.selectric-items {
  display: none;
  position: absolute;
  top: 100%;
  left: 0;
  background: white;
  border: 1px solid $borderColor;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  z-index: -1;
  text-align: left;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.07);
  .selectric-scroll {
    height: 100%;
    overflow: auto; } }

.selectric-above {
  .selectric-items {
    top: auto;
    bottom: 100%;
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px; }
  &.selectric-open {
    .selectric {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
      border-bottom-left-radius: 8px;
      border-bottom-right-radius: 8px; } } }

.selectric-items {
  ul {
    list-style: none;
    padding: 0;
    margin: 0;
    min-height: 20px; }
  li {
    list-style: none;
    padding: 0;
    margin: 0;
    font-size: 14px;
    line-height: 1.5;
    min-height: 20px;
    display: block;
    padding: 12px 30px;
    color: $fontColor50;
    cursor: pointer;
    transition: .15s $ease;
    &.selected {
      background: $borderColor;
      color: $fontColor; }
    &.highlighted {
      background: $colorAqua;
      color: white;
      &:hover {
        background: rgba($colorAqua,.8);
        color: white; } }
    &:hover {
      background: $borderColor;
      color: $fontColor; } }
  .disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    user-select: none; }
  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      user-select: none;
      background: none;
      color: #444; }
    &.disabled li {
      filter: alpha(opacity = 100);
      opacity: 1; }
    li {
      padding-left: 25px; } } }

/* MINI */
.selectric--mini {
  .selectric {
    border-radius: 15px;
    .label {
      margin: 0 10px 0 0px;
      font-size: 14px;
      padding: 8px 20px; }
    .button {
      margin-right: 0;
      height: 36px;
      width: 40px;
      color: $fontColor50; } }
  .selectric-items {
    li {
      padding: 8px 20px; } }
  &.selectric-open {
    .selectric {
      // border-color: #c4c4c4
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0; } } }


.selectric-selectric-no-bg {
  .selectric {
    background: transparent;
    box-shadow: none;
    border-radius: 0;
    .label {
      color: $colorViolet;
      margin-right: 0;
      padding: 0;
      font-size: 14px; }
    .button {
      display: none; } }
  .selectric-items {
    left: auto;
    right: 0;
    top: 130%;
    min-width: 150px;
    border-radius: 8px;
    overflow: hidden;
    li {
      padding: 10px 15px;
      font-size: 13px; } } }


.selectric-selectric-as-select {
  width: 100%;
  .selectric {
    background: #F5F5F5;
    box-shadow: 0 2px 4px 0 rgba(0,0,0,0.07);
    border-radius: 30px;
    padding: 12px 40px 12px 20px;
    .label {
      color: $fontColor;
      margin-right: 0;
      padding: 0;
      font-size: 14px; }
    .button {
      position: absolute;
      right: 5px;
      top: 50%;
      transform: translateY(-50%);
      color: $colorAqua;
      font-size: 7px;
      margin-right: 0;
      border: 0; } }

  .selectric-items {
    border-radius: 8px;
    overflow: hidden;
    li {
      padding: 10px 15px;
      font-size: 13px; } } }
