/* PLANS */
.plans {
  width: 100%;
  margin: 100px 0 80px;
  &__grid {
    display: flex;
    margin: -0px -15px; }

  &__col {
    flex: 0 1 33.333%;
    max-width: 33.333%;
    padding: 15px;
    &:hover {
      .plan-card {
        transform: translate(0,10px);
        box-shadow: 0 15px 20px -10px rgba(0,0,0,0.15);
        .btn {
          background: #8262CB; } } } } }

@include r($tablet) {
  .plans {
    &__grid {
      display: block; }

    &__col {
      flex: 0 0 auto;
      padding: 10px;
      width: 100%;
      max-width: 400px;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 40px; } } }

@include r($mobile) {
  .plans {
    margin: 60px 0 50px; } }

@include r($mobile-s) {
  .plans {
    margin: 30px 0 0px; } }

.plan-card {
  background: white;
  padding: 65px 20px 0px;
  text-align: center;
  color: $fontColor;
  box-shadow: 0 10px 30px 0 rgba(0,0,0,0.08);
  border-radius: 10px;
  cursor: pointer;
  transition: .25s $ease;
  &.is-active {
    margin-top: 30px;
    transform: translate(0,0) !important;
    .btn {
      background: #8262CB; } }
  &__icon {
    min-height: 75px;
    display: flex;
    align-items: center;
    justify-content: center; }
  &__name {
    margin-top: 60px;
    font-size: 30px;
    line-height: 1.2; }
  &__desc {
    margin-top: 25px;
    font-size: 14px;
    color: $fontColor50;
    line-height: 2.28;
    p {
      margin: 0; } }
  &__cta {
    margin-top: 60px;
    // btn height
    transform: translateY(21px); } }

@include r($desktop) {
  .plan-card {
    padding: 50px 15px 0px;
    &__name {
      margin-top: 45px;
      font-size: 26px; }
    &__desc {
      margin-top: 20px; }
    &__cta {
      margin-top: 45px; } } }

@include r($tablet) {
  .plans {
    &__col {
      &:hover {
        .plan-card {
          transform: translate(0,0); } } } }

  .plan-card {
    &.is-active {
      margin-top: 0; } } }



.plans-tabs {
  margin: 160px 0 -40px; }

.benefits {
  display: none;
  text-align: center;
  &__title {
    color: $colorAqua;
    font-size: 30px;
    line-height: 1.2; }
  &__subtitle {
    margin-top: 8px;
    font-size: 14px;
    line-height: 2;
    color: $fontColor50; }
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin: 40px -15px 0; }
  &__el {
    flex: 0 1 33.333%;
    max-width: 33.333%;
    padding: 30px;
    margin-bottom: 40px;
    color: $fontColor;
    &-name {
      margin-top: 30px;
      font-size: 16px;
      line-height: 2;
      font-weight: 500; }
    &-desc {
      margin-top: 15px;
      font-size: 12px;
      line-height: 2.2;
      color: $fontColor50;
      transition: .25s $ease;
      p {
        margin: 0;
        & + p {
          margin-top: 1em; } } }
    &:hover {
      .benefits {
        &__icon {
          transform: translate(0,7px); }
        &__el-desc {
          color: $fontColor; } } } }


  &__icon {
    size: 78px;
    margin-left: auto;
    margin-right: auto;
    background: #FFFFFF;
    box-shadow: 0 10px 30px 0 rgba(0,0,0,0.08);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $colorViolet;
    font-size: 30px;
    transition: .25s $ease;
    .ico-plan-benefit {
      &-1 {
        font-size: 36px; }
      &-2 {
        font-size: 39px; }
      &-3 {
        font-size: 33px; }
      &-4 {
        font-size: 28px; }
      &-5 {
        font-size: 29px; }
      &-6 {
        font-size: 30px; } }
    .ico-promo-benefiit {
      &-1 {
        font-size: 35px; }
      &-2 {
        font-size: 36px; }
      &-3 {
        font-size: 35px; }
      &-4 {
        font-size: 36px;
        transform: translateX(5px); }
      &-5 {
        font-size: 32px; }
      &-6 {
        font-size: 31px; } } }

  &__cta {
    margin-top: 50px;
    text-align: center; }

  &--line {
    .benefits {
      &__el {
        flex-basis: 50%;
        max-width: 50%;
        display: flex;
        padding: 15px;
        margin-bottom: 0px; }
      &__icon {
        flex: 0 0 auto;
        background: transparent;
        box-shadow: none; }
      &__content {
        flex: 1 1 auto;
        text-align: left; } } } }

@include r($desktop) {
  .plans-tabs {
    margin-top: 120px; }
  .benefits {
    &__list {
      margin-left: -10px;
      margin-right: -10px; }
    &__el {
      padding: 20px; }

    &--line {
      .benefits {
        &__el {
          padding: 10px; } } } } }


@include r($tablet) {
  .plans-tabs {
    margin-top: 80px; }

  .benefits {
    &__title {
 }      // font-size: 30px
    &__subtitle {
 }      // font-size: 13px

    &__el {
      flex-basis: 50%;
      max-width: 50%;
      margin-bottom: 30px;
      &-name {
        margin-top: 30px;
        font-size: 15px;
        line-height: 1.8; }
      &-desc {
        line-height: 2; } }
    &__icon {
      // size: 65px
      // .ico
      //   transform: scale(.8)
 }      //   transform-origin: center center

    &__cta {
      margin-top: 30px; }

    &--line {
      .benefits {
        &__el {
          padding: 10px;
          flex-basis: 100%;
          max-width: 100%; }
        &__icon {
          padding-top: 8px;
          size: 55px;
          text-align: left;
          justify-content: flex-start; } } } } }

@include r($mobile-s) {
  .benefits {
    margin-bottom: 60px;
    &__title {} } }



@include r(480) {
  .benefits {
    &__list {
      margin-top: 10px; }

    &__el {
      flex-basis: 100%;
      max-width: 100%;
      margin-bottom: 10px; } } }
