/* ITO QUOTES */
.ito-quotes {
  position: relative;
  margin: 150px 0 120px 0;
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 1;
    display: flex;
    flex-direction: column;
    pointer-events: none;
    &-wrapper {
      flex: 1 1 auto;
      display: flex;
      max-height: 820px;
      flex-direction: column;
      position: relative; }
    svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      min-width: 100%;
      min-height: 100%;
      width: 1920px;
      height: auto;
      overflow: visible; } }

  &__wrapper {
    min-height: 820px;
    display: flex;
    margin-left: -15px;
    margin-right: -15px; }

  &__slider {
    flex: 0 1 50%;
    max-width: 50%;
    min-width: 1px;
    min-height: 0;
    padding: 130px 15px 100px 0;
    display: flex;
    flex-direction: column;
    margin-left: -60px; }

  &__nav {
    position: relative;
    z-index: 2;
    flex: 0 1 50%;
    max-width: 50%;
    min-width: 1px;
    min-height: 0;
    padding: 0 15px;
    margin-right: -100px;
    &-el {
      position: absolute;
      font-size: 0;
      cursor: pointer;
      transition: filter .25s $ease;
      &:hover {
        // opacity: .8
        // filter: opacity(.8)
        .ito-quotes__nav-avatar {
          box-shadow: 0 15px 25px 0 rgba(29,36,84,0.52);
          transform: translateY(-3px);
          &::after {
            animation: shine 1s; } } }
      &--1 {
        top: 3%;
        left: 0%; }
      &--2 {
        top: 17%;
        right: 0%; }
      &--3 {
        top: 42%;
        left: 31%; }
      &--4 {
        top: 60%;
        left: 0; }
      &--5 {
        top: 60%;
        right: 5%; }
      &--6 {
        top: 87%;
        left: 25%; } }

    &-avatar {
      display: inline-block;
      position: relative;
      z-index: 2;
      border-radius: 50%;
      overflow: hidden;
      font-size: 0;
      box-shadow: 0 25px 50px 0 rgba(29,36,84,0.52);
      border: 5px solid transparent;
      transition: box-shadow .25s $ease, transform .25s $ease;
      &::after {
        position: absolute;
        top: 0;
        left: -75%;
        z-index: 2;
        display: block;
        width: 50%;
        height: 100%;
        background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .3) 100%);
        content: '';
        transform: skewX(-25deg); }
      &--1 {
        border-color: white;
        size: 190px; }
      &--2 {
        border-color: #28BAA9;
        size: 190px; }
      &--3 {
        border-color: #8064CA;
        size: 190px; }
      &--4 {
        border-color: #28BAA9;
        size: 100px; }
      &--5 {
        border-color: #FF277B;
        size: 137px; }
      &--6 {
        border-color: white;
        size: 167px; }
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 105%;
        transform: translate(-50%,-50%); } } }

  &__slider {
    position: relative; }
  &__slide {
    position: relative;
    flex: 0 0 auto;
    padding-top: 150px;
    padding-left: 30px; }

  &__quotemark {
    position: absolute;
    top: 90px;
    left: 0;
    font-size: 0;
    z-index: 1;
    pointer-events: none;
    .ico {
      transform: rotate(180deg);
      fill: rgba(#FFFFFF, .03);
      font-size: 280px; } } }

@keyframes shine {
  100% {
    left: 125%; } }

@include r($hd) {
  .ito-quotes {
    /* pseudo wide container */
    &__slider {
      margin-left: 0; }
    &__nav {
