.ito {
  .promo-benefits {
    position: relative;
    z-index: 2; }
  .hero {
    position: relative;
    z-index: 3; } }

.ito-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  display: flex;
  flex-direction: column;
  &__wrapper {
    flex: 1 1 auto;
    display: flex;
    max-height: 695px;
    flex-direction: column;
    position: relative;
    overflow: hidden; }
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: 2;
    transform: translate(-50%,-50%);
    width: 1500px;
    height: auto;
    overflow: visible; }
  svg {
    position: absolute;
    left: 50%;
    min-width: 100%;
    min-height: 100%;
    width: 1920px;
    overflow: visible;

    z-index: 1;
    top: auto;
    // min-height: 125px
    transform: translate(-50%,0);
    bottom: 0; } }


/* ITO counter */
