/* ito card */
.ito-card {
  flex: 1 0 auto;
  display: flex;
  flex-direction: column;
  background: white;
  box-shadow: 0 10px 30px 0 rgba(0,0,0,0.08);
  border-radius: 15px;
  transition: .25s $ease;
  &:hover {
    box-shadow: 0 15px 45px 0 rgba(0,0,0,0.18); }

  &__head {
    text-align: center;
    padding: 10px 30px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    background: #7F64CA;
    min-height: 85px;
    box-shadow: 0 10px 30px 0 rgba(0,0,0,0.08);
    border-radius: 15px 15px 0 0;
    &--green {
      background-color: $colorGreen; } }
  &__name {
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase; }

  &__content {
    border-radius: 0 0 15px 15px;
    padding: 20px 0 0;
    flex: 1 0 auto;
    display: flex;
    flex-direction: column; }

  &__icon {
    text-align: center; }

  &__scope {
    text-align: center;
    margin-top: 30px; }
  &__price {
    display: block;
    font-weight: 500;
    font-size: 20px; }
  &__minimum {
    color: $colorGreen;
    margin-top: 15px;
    display: block; }

  &__info {
    margin-top: 20px;
    text-align: center;
    background: #F8F8F8;
    padding: 20px; }
  &__dates {
    display: flex;
    flex-direction: column;
    font-size: 13px;
    line-height: 1.8;
    font-weight: 500; }

  &__distribution {
    margin-top: 18px;
    span {
      font-size: 16px;
      color: $fontColor50; } }

  &__total {
    margin-top: 8px;
    font-size: 18px;
    color: $colorViolet;
    font-weight: 500; }

  &__table {
    margin: 15px 0 -30px;
    &-row {
      display: flex;
      align-items: center;
      padding: 15px 10%;
      &:nth-child(even) {
        background-color: #F8F8F8; }

      span {
        display: inline-block;
        transition: .25s $ease;
        &:first-child {
          flex: 0 0 auto;
          min-width: 1px;
          font-size: 16px;
          color: $fontColor50;
          @include text-overflow; }
        &:last-child {
          font-size: 20px;
          font-weight: 500;
          min-width: 40px;
          color: $colorGreen;
          margin-left: auto; } }
      &:hover {
        span {
          &:first-child {
            color: $fontColor; }
          &:last-child {
            transform: translateX(-5px); } } } } }


  &__cta {
    padding-top: 25px;
    margin-top: auto;
    text-align: center;
    transform: translateY(22px);
    .btn {
      padding-top: 6px;
      padding-bottom: 6px; } } }



/* ITO slider */
.ito-slider {
  position: relative;
  z-index: 3;
  overflow: visible;
  width: 100%;
  display: flex;
  flex-direction: column;
  /* on desktop content is filled according to the background height */
  /* on mobile content is dynamic in heaight and background is sticket to top */
  /* token icon block is hidden/visible because of svg background overflow issues */
  &-bg {
    position: relative;
    z-index: 1;
    font-size: 0;
    .ito-slider__token {
      display: block; }
    svg {
      overflow: visible;
      position: relative;
      // top: 0
      left: 50%;
      // z-index: 1
      transform: translateX(-50%);
      min-width: 100%;
      width: 1920px;
      height: auto; } }

  .swiper-container {
    position: absolute 0;
    z-index: 2; }

  &__slide {
    position: relative;
    z-index: 2;
    flex: 0 0 auto;
    max-width: 100%;
    padding: 150px 0 110px;
    // background: #2F385B
    display: flex;
    flex-direction: column;
    color: white;
    > .container {
      // background: #2F385B
      flex: 1 0 auto;
      display: flex;
      flex-direction: column; } }

  &-next, &-prev {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    size: 65px;
    z-index: 3;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(white, .2);
    border-radius: 50%;
    color: rgba(white, .78);
    font-size: 20px;
    transition: .25s $ease;
    .ico {
      transition: .25s $ease; }
    &:hover {
      background: rgba(white, .75);
      color: $colorGreen; }

    &.swiper-button-disabled {
      opacity: .7;
      cursor: default; } }
  &-next {
    right: 50px;
    &:hover {
      .ico {
        transform: translateX(5px); } } }

  &-prev {
    left: 50px;
    &:hover {
      .ico {
        transform: translateX(-5px); } } }

  &__token {
    display: none;
    position: absolute;
    left: 50%;
    z-index: 3;
    bottom: -55px;
    transform: translateX(-50%);
    pointer-events: none; }

  &__wrapper {
    display: flex;
    flex: 1 0 auto;
 }    // align-items: center

  &__left {
    flex: 0 0 400px;
    max-width: 400px;
    display: flex;
    flex-direction: column;
    min-width: 1px;
    .benefits__icon {
      margin-left: 0;
      color: $colorGreen;
      font-size: 46px; } }
  &__title {
    font-size: 37px;
    font-weight: 500;
    margin: 0em 0 0.5em;
    padding-top: .7em;
    margin-top: auto; }

  &__table {
    width: 100%;
    margin-bottom: auto;
    border-collapse: collapse;
    max-width: 310px;
    td {
      padding: 18px 0;
      font-size: 18px; }
    tr {
      td:last-child {
        color: $colorGreen;
        font-weight: 700;
        padding-left: 5%; } } }

  &__right {
    flex: 0 1 550px;
    max-width: 550px;
    padding-left: 30px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: auto;
    padding-top: 25px;
 }    // margin-left: 70px

  &__image {
    // padding-top: 25px
    margin: auto 0;
    position: relative;
    width: 100%;
    font-size: 0;
    img {
      width: 100%; } }
  &__dot {
    position: absolute;
    z-index: 2;
    &--pd {
      right: 7px;
      top: 47%;
      span {
        left: 15px;
        top: -100px; } }
    &--bounty {
      top: 6px;
      left: 45%;
      span {
        bottom: 15px;
        right: 20px; } }

    &-wrap {
      position: relative;
      size: 22px;
      border-radius: 50%;
      background: #FAFCFD;
      border: 6px solid #7F64CA;
      &::after {
        display: inline-block;
        content: ' ';
        position: absolute;
        z-index: -1;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 100%;
        height: 100%;
        border-radius: 50%;
        transform: translate(-50%,-50%) scale(.7);
        animation-name: slideDotAnimation;
        animation-timing-function: ease-in-out;
        animation-iteration-count: infinite;
        animation-fill-mode: both;
        animation-duration: 2s;

        border: 12px solid rgba(127,100,202,0.30); }
      span {
        position: absolute;
        z-index: 3;
        background: white;
        font-size: 18px;
        font-weight: 700;
        color: $colorGreen;
        box-shadow: 11px 6px 30px 0 rgba(0,0,0,0.24);
        border-radius: 5px;
        padding: 23px 18px;
        pointer-events: none;
        opacity: 0;
        transform: translate(5px,-5px);
        transition: .25s $ease; }
      &:hover {
        span {
          opacity: 1;
          transform: translate(0,0); } } } } }

@keyframes slideDotAnimation {
  0% {
    opacity: 0.6;
    transform: translate(-50%,-50%) scale(0.7); }
  50% {
    opacity: 1;
    transform: translate(-50%,-50%) scale(1.3); }
  100% {
    opacity: 0.6;
    transform: translate(-50%,-50%) scale(0.7); } }


