// COMMON TYPOGRAPHY STYLES
.heading {
  font-weight: 500;
  font-size: 36px; }

@include r($mobile) {
  .heading {
    font-size: 32px; } }

@include r($mobile-s) {
  .heading {
    font-size: 30px;
    margin-bottom: .5em; } }

// colors
.c-black {
  color: $fontColor; }
.c-white {
  color: white; }
.c-black-muted {
  color: $fontColor50; }
.c-aqua {
  color: $colorAqua; }
.c-violet {
  color: $colorViolet; }
.c-green {
  color: $colorGreen; }
.c-lime {
  color: $colorLime; }
